.index-slider-section{
	padding: 20px 0 60px;
}

.image-slider__slides.slick-slider{

	.slick-list {

		width: 100vw;

		.slick-track{

			display: flex;

			

			.slick-slide{
				padding: 10px;
			}

			.slick-slide:hover .image-slider__slide{
				box-shadow: 0px 11px 23px rgba(0, 0, 0, 0.25);
				cursor: pointer;
			}

			.slick-slide:hover .image-slider__slide-inner .simple-button{
				background-color: #ff2b2b;
    			color: #fff;
			}

		} 

	}
}

.image-slider{
	position: relative;
	display: flex;
    flex-wrap: wrap;
	justify-content: center;
	&:before{
		// content: "";
		position: absolute;
		z-index: -1;
		top: 20px;
		left: 20px;
		height: 100%;
		width: calc(100% - 40px);
		background-color: rgba(0,0,0,0.1);
	}
	&:after{
		// content: "";
		position: absolute;
		z-index: -1;
		top: 40px;
		left: 40px;
		height: 100%;
		width: calc(100% - 80px);
		background-color: rgba(0,0,0,0.1);
	}
	&__slides{
		transition: .3s;
		&:not(.slick-initialized){
			height: 540px;
			opacity: 0;
			overflow: hidden;
		}
	}

	&__slides.onetest{
		.slick-track{
			width: 100% !important;
		}
		.image-slider{
			width: 100%;
		}
		.image-slider__slide-inner{
			height: 100%;
			width: 100%;
			padding: 5vw 35vw 5vw 4vw;;
		}
		.slick-slide{
			width: 100% !important;
		}
		.slick-list{
			width: 100%;
		}
		.simple-button{
			margin-top: 13rem;
		}
	}

	&__slide{

		&-inner{
			display: flex;
			flex-direction: column;
			justify-content: space-between;
			height: 320px;
			// width: 600px;
			padding: 40px 5% 65px 40px;
			background-size: cover;
			background-repeat: no-repeat;
			.simple-button{
				background-color: transparent;
    			color: #ff2b2b;
			}
			
		}
		&-title{
			font-family: Montserrat;
			font-style: normal;
			font-weight: bold;
			font-size: 32px;
			line-height: 40px;
			display: block;
			color: #fff;
			text-transform: uppercase;
		}
		&-button{
			width: 225px;
			height: 60px;
		}
	}
	&__arrows{
		display: flex;
		align-items: center;
		justify-content: center;
		// position: absolute;
		z-index: 20;
		// bottom: 70px;
    	// right: 65px;
		width: 200px;
		height: 32px;
		
		margin-top: 15px;
	}
	&__arrow{
		display: flex;
		align-items: center;
		justify-content: center;
		position: relative;
		width: 50%;
		height: 100%;
		background: transparent;
		border: none;
		cursor: pointer;
		&:hover{
			> svg{
				fill: $color-blue-dark;
			}
		}
		&_prev{
			&:after{
				// content: "";
				position: absolute;
				top: 5px;
				right: 0;
				width: 1px;
				height: calc(100% - 10px);
				background-color: #d8d8d8;
				opacity: 0.5;
			}
		}
		&_next{
			> svg{
				transform: rotate(180deg);
			}
		}
		> svg{
			fill: #fff;
			transition: .3s;
			fill: #C4C4C4;
			width: 20px;
			height: 20px;
		}
	}



	.slick-dots{
		display: flex;
		align-items: center;
		justify-content: center;
		position: absolute;
		bottom: -20px;
		left: 0;
		width: 100%;
		transform: translate(0px, 15px);
		li{
			display: block;
			margin: 0 5px;
			width: 7px;
			height: 7px;
			border-radius: 5px;
			background-color: #dadada;
			transition: .3s;	

			&.slick-active{
				width: 9px;
				height: 9px;
				background-color: #E94339;
				// border: 1px solid #2d303f;
			}
			button{
				display: none;
			}
		}
	}
}

@media(max-width: 1819px){
	.slick-slider{

		.slick-list {
	
			.slick-track{
	
				.slick-slide:nth-child(2){
	
					margin: 0 !important;
	
				}
	

				.image-slider__slide-inner{
					width: 100%;
				}

			} 
	
		}
	}
}


@media(max-width: 1463px){
	.image-slider{
		height: 36vw;
		&__arrows{
			bottom: 3.5vw;
			right: 3.5vw;
		}
		&__slide{
			&-inner{
				height: 36vw;
				// padding: 3.5vw 40vw 3.5vw 3.5vw;
				// padding: 0 !important;
				width: 100%;
			}
			&-title{
				font-size: 3.3vw;
				line-height: 4.7vw;
			}
			&-button{
				width: 15vw;
				height: 4vw;
				font-size: 1vw !important;
			}
		}

	}
	.slick-slider{

		.slick-list {
	
			.slick-track{
	
				.slick-slide:nth-child(2){
	
					margin: 0 !important;
	
				}
	
			} 
	
		}
	}
}

@media(max-width: 767px){
	.index-slider-section{
		padding: 10px 0;
	}

	.image-slider{
		height: 35vw;
		&:before{
			top: 4px;
			left: 4px;
			width: calc(100% - 8px);
		}
		&:after{
			top: 8px;
			left: 8px;
			width: calc(100% - 16px);
		}
		&__arrows{
			display: none;
		}
		&__slide{
			&-inner{
				height: 35vw;
				padding: 5vw 35vw 5vw 4vw;
			}
		}
	}

	.slick-slider{

		.slick-list {
	
			.slick-track{

				.slick-slide{
					padding: 0px !important;
				}
	
				.simple-button{
					display: none;
				}
	
			} 
	
		}
	}

}