.tab-pane{
	padding: 15px 25px;
	&__characteristic-row{
		display: flex;
		align-items: flex-start;
		padding: 8px 0 10px;
		text-transform: capitalize;
		&-title{
			display: flex;
			width: 46%;
			min-width: 46%;
			color: #2d303f;
			font-size: 14px;
			font-weight: 700;
			line-height: 18px;
			span {
				flex: 0 1 auto;
			}
			i {
				display: block;
				border-bottom: 1px solid #e5e5e5;
				flex: 1 0 auto;
				margin-bottom: 4px;
			}
		}
		&-value{
			display: block;
			color: #333333;
			font-size: 16px;
			font-weight: 400;
			line-height: 20px;
		}
	}
}

.product-tabs {
    background-color: #fff;
}
.fade:not(.show) {
    opacity: 0;
    display: none;
}

.nav-tabs {

	.nav-link {
		border: none;
		border-bottom: 2px solid transparent;
		color: #2d303f;
		font-size: 12px;
		font-weight: 700;
		line-height: 16px;
		height: 40px;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 0 40px;
		&:hover, &:focus {
			border-bottom-color: $color-red;
		}
		&.active {
			color: #fff;
			background-color: #2d303f;
			border-radius: 0;
			border-bottom-color: #2d303f;
		}
	}
}

.tab-content {
    padding: 0 15px;
}

@media (max-width : 767px) {
	.tab-pane{
		padding: 10px 2px;
	}
}

@media (max-width : 575px) {
	.tab-pane{
		&__characteristic-row{
			flex-direction: column;
			padding: 8px 0 0;
			&-title{
				width: 100%;
				i {
					display: none;
				}
			}
			&-value{
				font-size: 14px;
			}
		}
	}
}