.header {
    flex: 0 1 auto;
    position: fixed;
    z-index: 100;
    top: 0;
    left: 0;
    width: 100%;
    max-height: 120px;
    transition: transform 500ms ease;
	
	&-desktop{
		box-shadow: 0 2px 12px rgba(0, 0, 0, 0.09);
	}
  
	&-callback{
		display: flex;
		align-items: center;
		justify-content: center;
		flex: 0 0 225px;
		height: 45px;
		margin: 0 0 0 50px;
		border-radius: 22px;
		border: 1px solid $color-red;
		transition: .3s;
		$button: &;
		&:hover{
			background-color: $color-red;
			#{$button}__icon{
				> svg{
					fill: #fff;
				}
			}
			#{$button}__text{
				color: #fff;
			}
		}
		&__icon{
			> svg{
				fill: $color-red;
				margin-right: 10px;
			}
		}
		&__text{
			color: $color-red;
			font-size: 15px;
			font-weight: 600;
		}
	}

    &-middle {
		background-color: $color-blue-dark;
		&__wrapper{
			display: flex;
			align-items: center;
			justify-content: space-between;
			height: 85px;
			&-left{
				display: flex;
				align-items: center;
				flex: 1 0 auto;
				height: 100%;
			}
			&-right{
				display: flex;
				align-items: center;
				justify-content: flex-end;
				flex: 1 0 auto;
				height: 100%;
			}
		}

		&__menu-list{
			display: flex;
			height: 100%;
			padding-left: 10px;
			li{
				height: 100%;
				a{
					display: flex;
					align-items: center;
					height: 100%;
					padding:0 15px;
					color: #fff;
					font-size: 15px;
					font-weight: 400;
					transition: .3s;
					&:hover{
						color: $color-red;
					}
				}
			}
		}

		&__email{
			display: block;
			border-bottom: 1px solid #fff;
			opacity: 0.36;
			color: #fff;
			font-size: 15px;
			font-weight: 400;
			transition: .3s;
			&:hover{
				opacity: 1;
				color: #fff;
			}
		}
	
		&__phone{
			display: block;
			margin: 0 0 0 50px;
			color: #fff;
			font-size: 18px;
			font-weight: 700;
			transition: .3s;
			&:hover{
				color: $color-red;
			}
		}
    }

    &-bottom {
		border-top: 1px solid rgba(216, 216, 216, 0.16);
		background-color: $color-blue-dark;
		transition: box-shadow 500ms ease;
		
		&__menu-list{
			display: flex;
			height: 100%;
			width: 80%;
			justify-content: space-between;
			padding-right: 10px;
			li{
				height: 100%;
				a{
					display: flex;
					align-items: center;
					position: relative;
					padding: 0;
					height: 100%;
					color: #fff;
					font-size: 18px;
					font-weight: 600;
					transition: .3s;
					&:after{
						content: "";
						position: absolute;
						top: -1px;
						left: 0;
						width: 100%;
						height: 2px;
						background-color: $color-red;
						transform: scale(0, 1);
						transition: transform .4s;
						transform-origin: right top;
					}
					&:hover{
						color: $color-red;
						&:after{
							transform: scale(1, 1);
							transform-origin: left top;
						}
					}
					> svg{
						fill: $color-red;
						margin: 0 10px 0 0;
					}
				}
			}
		}
	}
	
	&-search{
		position: relative;
		flex: 1 0 auto;
		height: 100%;
		border-left: 1px solid rgba(216, 216, 216, 0.16);
		input{
			width: 100%;
			height: 100%;
			padding: 0 65px 0 25px;
			background-color: transparent;
			border: none;
			color: rgba(255,255,255, 0.36);
			font-size: 15px;
			font-weight: 400;
		}
		button{
			display: flex;
			justify-content: center;
			align-items: center;
			position: absolute;
			top: 0;
			right: 0;
			width: 65px;
			height: 100%;
			border: none;
			background: transparent;
			cursor: pointer;
			&:hover{
				> svg{
					fill: $color-red;
				}
			}
			> svg{
				fill: rgba(255, 255, 255, 0.38);
				transition: .3s;
			}
		}
		&__ajax-search{
			padding: 10px 20px 0;
			
			> ul{
				display: none;
				padding: 10px 0;
				border-radius: 0;
				border: 1px solid $color-blue-dark;
				background-color: $color-blue-dark;
				border-top: 1px solid rgba(216, 216, 216, 0.16);
				position: absolute;
				z-index: 200;
				left: 0;
				top: 100%;
				width: 100%;
				max-height: 500px;
				overflow-y: scroll;
				li{
					a, span{
						display: block;
						padding: 8px 20px;
						color: #fff;
						font-size: 15px;
						font-weight: 600;
						line-height: 21px;
						background-color: $color-blue-dark;
						transition: .2s;
						&:hover{
							color: $color-red;
						}
					}
				}

				// ul{
				// 	padding: 2px 0;
				// 	li{
				// 		a{
							
				// 			padding: 5px 20px;
				// 			color: #373a3c;
				// 			font-size: 16px;
				// 			font-weight: 400;
				// 			line-height: 22px;
				// 			background-color: transparent;
				// 			&:hover{color: $color-general;}
				// 		}
				// 	}
				// }
			}
		}
	}

    &-shop {
        display: flex;
		align-items: center;
		height: 65px;
    }

    &-mobile {
        display: flex;
        justify-content: space-between;
        box-shadow: 0 2px 12px rgba(0, 0, 0, 0.09);
		background-color: $color-blue-dark;
		height: 60px;
		
		&__logo{
			display: flex;
			align-items: center;
			justify-content: center;
			height: 100%;
			font-size: 22px;
			color: #fff;
			font-weight: 400;
			&:hover{
				color: #fff;
			}
		}

		.mobile-cart-link{
			display: flex;
			align-items: center;
			justify-content: center;
			height: 100%;
			width: 60px;
			background-color: $color-red;

			.icon{
				display: flex;
				align-items: center;
				position: relative;
				height: 25px;
				
				> svg{
					fill: #fff;
					margin: 0 5px 0 0;
				}
				.number{
					display: block;
					color: #fff;
					font-size: 13px;
					font-weight: 400;
				}
			}
		}
    }
}

@media(max-width: 1463px){
	.header{
		&-callback{
			margin: 0 0 0 15px;
		}
		&-middle{
			&__menu-list{
				li{
					a{
						padding: 0 10px;
						font-size: 13px;
					}
				}
			}
			&__phone{
				margin: 0 0 0 15px;
				font-size: 15px;
			}
			&__email {
				font-size: 13px;
			}
		}
		&-bottom{
			&__menu-list{
				li{
					a{
						padding: 0;
						font-size: 17px;
						&:after{
							width: 100%;
						}
					}
				}
			}
		}
	}
}

@media(min-width: 1200px) {
    .header {
        &.is-hidden {
			// transform: translateY(-100%);
			transform: translateY(-85px);
        }

        // &.is-hidden &-bottom {
