.index-sizes{
	padding: 90px 0;
	background-color: #fff;
	background-image: url('/static/images/tire-colored.png');
	background-repeat: no-repeat;
	background-position: 40vw bottom;
	&__title{
		display: block;
		padding: 0 0 65px;
		color: $color-blue-dark;
		font-size: 36px;
		font-weight: 700;
		text-transform: uppercase;
	}
	&__sizes{
		display: flex;
		flex-wrap: wrap;
	}
	&__size-link{
		display: flex;
		align-items: center;
		justify-content: center;
		width: 125px;
		height: 40px;
		margin: 0 15px 25px 0;
		border-radius: 25px;
		border: 1px solid #e3e3e3;
		background-color: #fff;
		color: $color-red;
		font-size: 15px;
		font-weight: 600;
		text-transform: uppercase;
		transition: .2s;
		&:hover{
			background-color: $color-red;
			border-color: $color-red;
			color: #fff;
		}
	}
}


@media(max-width: 1199px){
	.index-sizes{
		background-position: 22vw bottom;
	}
}

@media(max-width: 991px){
	.index-sizes{
		padding: 50px 0;
		background-image: none;
		&__title{
			padding: 0 0 30px;
		}
	}
}

@media(max-width: 767px){
	.index-sizes{
		
		&__title{
			font-size: 30px;
		}
	}
}

@media(max-width: 575px){
	.index-sizes{
		padding: 30px 0 20px;
		&__title{
			padding: 0 0 20px;
			font-size: 18px;
			line-height: 24px;
		}
		&__sizes{
			margin: 0 -2px;
		}
		&__size-link{
			width: calc(100% / 3 - 4px);
			margin: 0 2px 9px;
			height: 35px;
			font-size: 13px;
			&:last-child{
				display: none;
			}
		}
	}
}

