.conditions{
	border: 1px dashed #ccc;
	padding: 15px 0;
	display: flex;
	&__block{
		width: 50%;
		padding: 20px 15px 20px 35px;
		&:not(:last-child){
			border-right: 1px solid #e7e7e7;
			padding-right: 20px;
    		margin-right: 19px;
		}
		&-title{
			display: flex;
			align-items: center;
			padding-bottom: 20px;
			> svg{
				margin: 0 12px 0 0;
				&.black {
					fill: #2d303f;
				}
				&.red {
					fill: $color-red;
				}
			}
			b{	
				display: block;
				color: #2d303f;
				font-size: 16px;
				font-weight: 700;
				line-height: 20px;
			}
		}
		&-text{
			color: #2d303f;
			font-size: 15px;
			font-weight: 400;
			line-height: 24px;
		}
	}
}

@media (max-width : 767px) {
	.conditions{
		margin: 10px 0;
	}
}

@media(max-width: 575px) {
	.conditions {
		flex-direction: column;
		&__block {
			width: 100%;
			padding: 15px;
			&:not(:last-child) {
				border: none;
				margin: 0;
				padding: 15px;
			}
			&-title {
				padding-bottom: 10px;
			}
		}
	}
}