.filters{
	&__form{
		border: 1px solid #e5e5e5;
		// background-color: #fff;
	}
	
	&__footer{
		padding: 16px 12px;
		&-result{
			color: #373a3c;
			font-size: 14px;
			line-height: 18px;
			font-weight: 400;
			text-align: center;
			padding-bottom: 21px;
		}
		&-buttons{
			display: flex;
			justify-content: space-between;
			input{
				margin-bottom: 5px;
				cursor: pointer;
				height: 30px;
				flex: 0 0 calc(50% - 5px);
				border-radius: 15px;
				font-size: 12px;
				font-weight: 400;
				line-height: 16px;
			}
		}
	}

	&__mobile-footer{
		display: flex;
		flex-direction: column;
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		height: 100px;
		padding: 16px 12px;
		border-top: 1px solid #e5e5e5;
	}
	&__mobile-result{
		color: #373a3c;
		font-size: 16px;
		font-weight: 400;
		text-align: center;
		padding-bottom: 15px;
	}
	&__mobile-controllers{
		display: flex;
		justify-content: space-between;
		button{
			margin-bottom: 5px;
			cursor: pointer;
			height: 30px;
			flex: 0 0 calc(50% - 5px);
			font-size: 15px;
			font-weight: 400;
			line-height: 21px;
		}
	}
	&__mobile-reset{
		color: #373a3c;
		border: 1px solid #ccc;
		background-color: #fff;
		border-radius: 4px;
		transition: .3s;
		&:hover{
			color: $color-red;
			border-color: $color-red;
		}
	}
}

.search_field {
	width: 100%;
	height: 30px;
	color: #373a3c;
	font-size: 15px;
	font-weight: 400;
	line-height: 21px;
	padding: 0 16px;
	border-radius: 15px;
	border: 1px solid #cccccc;
	background-color: #ffffff;
	margin-bottom: 10px;
	&::placeholder{
		color: #9b9b9b;
		font-size: 14px;
	}
}

.popular-sizes {
	&__title {
		color: #2d303f;
		font-size: 16px;
		font-weight: 700;
		line-height: 20px;
		margin-bottom: 13px;
	}
}

.filter-popular {
	display: flex;
	flex-wrap: wrap;
	width: calc(100% + 8px);
	margin-left: -4px;
	padding-bottom: 20px;
	&__item {
		width: calc(10% - 8px);
		height: 36px;
		border-radius: 25px;
		border: 1px solid #e3e3e3;
		background-color: #ffffff;
		display: flex;
		align-items: center;
		justify-content: center;
		margin: 0 4px 8px;
		transition:.3s;
		&:hover {
			background-color: $color-red;
			border-color: $color-red;
			.filter-popular__text {
				color: #fff;
			}
		}
	}
	&__text {
		color: #2d303e;
		font-size: 13px;
		font-weight: 600;
		text-transform: uppercase;
	}
}


@media (max-width: 1366px) {
	.filter-popular {
		&__item {
			width: calc(12.5% - 8px);
		}
	}
}

@media (max-width: 1199px) {
	.filters{
		background-color: #fff;
		position: fixed;
		z-index: 800;
		width: 270px;
		height: 100%;
		right: 0;
		top: 0;
		overflow-y: auto;
		transform: translateX(110%);
		transition: .3s;
		&.opened{
			transform: translateX(0%);
		}
		&__footer{
			display: none;
		}
		&__mobile-wrapper{
			height: calc(100% - 100px);
			overflow-y: auto;
		}
	}
}

@media (max-width: 991px) {
	.filter-popular {
		&__item {
			width: calc(20% - 8px);
		}
	}
}

@media (max-width: 575px) {
	.filter-popular {
		&__item {
			width: calc(25% - 8px);
		}
	}
	
}

@media (max-width: 400px) {
	.filter-popular {
		&__item {
			width: calc(33.334% - 8px);
		}
	}
}