.whitewall {
    background-color: rgba(255,255,255, 0.5);
    width: 100%;
    height: 100%;
    z-index: 110;
    position: absolute;
    display: none;
    &.active{
        display: block;
    }
&:before {
	content: '';
	display: block;
	background: url('/static/images/reload.png') no-repeat;
	background-size: cover;
	width: 40px;
	height: 40px;
	animation: 2s spin infinite linear;
	z-index: 111;
	margin: 10% 50%;

    }
}


@keyframes spin {
  from {
    transform: rotate(0deg);
  }
  to {
    transform: rotate(360deg);
  }
}

@media (min-width: 1200px) {
  .popular-sizes .whitewall {
    display: none;
  }
}



