.product-slider{
	&__container{
		position: relative;
		border-radius: 4px;
		border: 1px solid #e5e5e5;
		background-color: #fff;
		overflow: hidden;
	}
	&__container-slide{
		display: flex !important;
		align-items: center;
		justify-content: center;
		padding: 15px;
		height: auto;
		outline: none;
		img{
			width: 100%;
			max-width: 100%;
			max-height: 100%;
			object-fit: contain;
		}
	}
	&__carousel{
		padding-top: 5px;
		overflow: hidden;
		.slick-list{
			margin: 0 -3px;
			.slick-track{
				min-width: 100%;
			}
		}
		.slick-slide{
			margin: 0 3px;
			&.slick-current{
				.product-slider__carousel-item{
					border-color: $color-general;
				}
			}
		}
	}
	&__carousel-item{
		display: flex !important;
		align-items: center;
		justify-content: center;
		height: 94px;
		padding: 5px;
		outline: none;
		border-radius: 4px;
		border: 1px solid #e5e5e5;
		background-color: #fff;
		img{
			max-width: 100%;
			max-height: 100%;
		}
	}
	.slick-dots{
		display: flex;
		justify-content: center;
		position: absolute;
		bottom: 7px;
		width: 100%;
		li{
			&.slick-active{
				button{
					background-color: $color-general;
				}
			}
			button{
				width: 11px;
				height: 11px;
				border-radius: 100%;
				font-size: 0;
				border: none;
				box-shadow: none;
				outline: none;
				margin: 0 6px;
				padding: 0;
				background-color: #dbdbdb;
			}
		}
	}
}


.product-slider-wrap {
	position: relative;
	.badges {
		display: flex;
		flex-direction: column-reverse;
		align-items: flex-end;
		position: absolute;
		top: 13px;
		right: 13px;
	}
	.badge {
		padding: 5px 10px;
	}
}

.product-slider-wrap {
	.product-card-params {
		top: 17px;
		left: 20px;
		&:not(:last-child) {
			margin-bottom: 13px;
		}
		& > svg {
			max-width: 100%;
			max-height: 100%;
			width: 100%;
			height: 100%;
		}
	}
}



@media (max-width : 767px) {
	.product-slider{
		&__container{
			&:after{
				display: none;
			}
		}
		&__container-slide{
			height: 320px;
		}
		&__carousel{
			display: none;
		}
	}
}

@media (max-width : 575px) {
	.product-slider{
		&__container{
			&:after{
				
			}
		}
		&__container-slide{
			height: 260px;
		}
		&__carousel{
			
		}
	}
}