.load_content{
	display: block;
    height: 48px;
    border-radius: 24px;
    background-color: #e0e0e0;
    border: none;
    max-width: 720px;
    width: 100%;
	margin: 0 auto 30px;
	color: #2d303f;
	font-size: 14px;
	font-weight: 600;
	&:hover {
		background-color: $color-red;
		color: #fff;
	}
}

.page-item {
    width: auto;
    height: 40px;
    min-width: 40px;
	.page-link {
		color: #2d303f;
		font-size: 16px;
		font-weight: 400;
		line-height: 21px;
		display: flex;
		align-items: center;
		justify-content: center;
		padding: 0;
		height: 100%;
		width: 100%;
	}
	&:first-child {
		.page-link {
			border-radius: 100px 4px 4px 100px;
		}
	}
	&:last-child {
		.page-link {
			border-radius: 4px 100px 100px 4px;
		}
	}
	&.active {
		.page-link {
			background-color: $color-red;
			border-color: $color-red;
		}
	}
}


@media (max-width: 767px) {
  
	.load_content {
		margin: 0 0 15px;
		height: 40px;
		font-size: 16px;
	}
}

@media(max-width: 575px) {
	.page-item {
		height: 30px;
		min-width: 30px;
		.page-link {
			font-size: 10px;
			line-height: 16px;
		}
	}
}