.index-forms{
	padding: 32px 0;
}

.form-block{
	margin: 45px 0 0;
	
	&__title{
		display: inline-block;
		padding: 4px 22px;
		background-color: $color-blue-dark;
		color: #fff;
		font-size: 36px;
		font-weight: 700;
		text-transform: uppercase;
	}
	
	&__container{
		display: flex;
		align-items: flex-end;
		flex-wrap: wrap;
		position: relative;
		padding: 20px 35px 30px;
		box-shadow: 0 11px 23px rgba(0, 0, 0, 0.11);
		background-color: #fff;
	}
	&__image{
		display: flex;
		justify-content: flex-end;
		position: absolute;
		z-index: -1;
		top: -105px;
		right: 30px;
		width: 210px;
		height: 210px;
		img{
			max-width: 100%;
			max-height: 100%;
		}
	}
	&__select-block{
		flex: 1 0 auto;
		max-width: calc(100% / 2 - 30px);
		min-width: calc(100% / 3 - 30px);
		margin: 15px;

		&-title{
			display: flex;
			padding: 0 0 5px;
			color: #2d303f;
			font-size: 16px;
			font-weight: 400;
			
		}
		&-icon{
			display: flex;
			align-items: center;
			justify-content: center;
			flex: 0 0 35px;
			> svg{
				fill: $color-red;
			}
		}
		&-selector{
			width: 100%;
			height: 50px;
		}
	}

	&__button{
		height: 50px;
		flex: 1 0 auto;
		max-width: calc(100% / 2 - 30px);
		min-width: calc(100% / 3 - 30px);
		margin: 15px;
	}
}

@media(max-width: 1463px){
	.form-block{
		&__container{
			padding: 15px 15px 20px;
		}
	}
}

@media(max-width: 1199px){
	.form-block{
		margin: 45px 0 30px;
		&__container{
			
		}
	}
}

@media(max-width: 767px){
	.form-block{
		
		&__select-block, &__button{
			max-width: calc(100% / 2 - 10px);
			min-width: calc(100% / 3 - 10px);
			margin: 10px 5px;
		}
	}
}

@media(max-width: 575px){
	.index-forms{
		padding: 15px 0;
	}
	.form-block{
		margin: 15px 0;
		&__title{
			padding: 5px 16px;
			font-size: 18px;
		}
		&__image{
			top: -40px;
			right: 10px;
			width: 80px;
			height: 80px;
		}
		&__container{
			padding: 12px 15px 25px;
		}
		&__select-block, &__button{
			flex: auto;
			width: 100%;
			max-width: none;
			min-width: auto;
			margin: 6px 5px;
		}
		&__select-block{

			&-title{
				font-size: 14px;
			}
			&-selector{
				height: 35px;
				font-size: 14px;
			}
		}
		&__button{
			margin-top: 15px;
			height: 35px;
		}
	}
}