.filter-counter{
	width: 106px;
    height: 36px;
    line-height: 32px;
    background-color: $color-red;
    text-align: center;
    position: absolute;
    left: calc(100% - 10px);
    transition: top .3s;
    display: none;
    z-index: 99;
	text-decoration: none !important;
	border-radius: 0 100px 100px 0;
	input{
		background-color: transparent;
		border: none;
		width: 100%;
		height: 100%;
		position: absolute;
		left: 0;
		top: 0;
		z-index: 99;
		opacity: 0;
		cursor: pointer;
	}
	span{
		color: #fff;
		font-size: 12px;
		font-weight: 400;
		border-bottom: 1px dashed;
		padding-bottom: 2px;
	}
	&:after{
		content: '';
		width: 8px;
		height: 8px;
		position: absolute;
		display: block;
		top: 0;
		bottom: 0;
		margin: auto;
		left: -4px;
		z-index: 9;
		background-color: $color-red;
		transform: rotate(-45deg);
	}
}
form.count-filters {
	display: flex;
	align-items: center;
	.count-filters__item:first-child {
		margin-left: 30px;
	}
	.count-filters__item {
		border-radius: 25px;
		border: 1px solid #e3e3e3;
		background-color: #ffffff;
		display: flex;
		align-items: center;
		padding: 10px 20px;
		margin: 15px;
		max-height: 47px;
		cursor: pointer;
		&.disabled {
			cursor: not-allowed;
			background-color: #e3e3e3;
			label {
				cursor: not-allowed;
			}
			&:hover {
				background-color: #e3e3e3;
			}
		}
		label {
			margin: 0;
			color: #2d303f;
			font-size: 16px;
			font-weight: 600;
			margin-left: 10px;
			cursor: pointer;
			display: grid;
		}
		small {
			margin-top: -5px;
		}
		&:hover {
			background-color: #ff2b2b;
		}
		&:hover > label {
			color: #fff;
		}
		&:hover > svg {
			fill: #fff;
		}
		span {
			font-size: 32px;
			color: #fff;
			position: relative;
			right: -5px;
			opacity: 0;
		}
	}
}

.sort-block {
	background: #fff;
	input[type="checkbox"] {
		display: none;
	}
	hr {
		margin: 0;
	}
}

.buttonwall {
	display: none;
	position: absolute;
	width: 100%;
	height: 100%;
	background: white;
	opacity: 0.5;
	z-index: 99;
	cursor: not-allowed;
	&.active {
		display: block;
	}
}

@media (max-width: 1199px) {
	.filter-counter {
		display: none !important;
	}
}
@media (max-width: 575px) {
	.count-filters {
		flex-wrap: wrap;
		&__item {
			margin: 5px !important;
			:first-child {
				margin-left: 0 !important;
			}
		}
	}
}