.seo-scroller{
	padding: 80px 0;
	.os-scrollbar{
		&.os-scrollbar-vertical{
			width: 7px;
		}
		&-track{
			background-color: rgba(45, 48, 63, 0.11) !important;
		}
		&-handle{
			border-radius: 0 !important;
			background-color: $color-red !important;
			width: 5px !important;
			opacity: 1 !important;
			margin: 0 0 0 -1px;
		}
	}
	.os-padding{
		.os-content{
			padding: 0 15px 0 0 !important;
		}
	}
	&_index-scroller{
		padding: 0 0 90px;
		margin: 0;
		border: none;
		.os-scrollbar{
			&-track{
				background-color: #e9e9e9 !important;
			}
		}
	}
	&__seo-images{
		> img{
			display: none;
		}
		.img_row{
			display: flex;
			justify-content: space-between;
			.seo_img{
				height: 240px;
				margin-bottom: 20px;
				&:not(.big){
					width: calc(100% / 2 - 10px);
				}
			}
		}
		.seo_img{
			width: 100%;
			&.big{
				height: 310px;
			}
			&.large{
				height: 400px;
			}
			img{
				width: 100%;
				height: 100%;
				object-fit: cover;
			}
		}
	}
	&__seo-text{
		height: 100%;
		overflow-y: scroll;
	}
	
}

@media (max-width : 991px) {
	.seo-scroller{
		.seo-scroller__seo-images{
			padding-bottom: 20px;
			.seo_img{
				&.large{
					height: 450px;
				}
			}
		}
		.seo-scroller__seo-text{
			height: 350px;
		}
	}
}

@media (max-width : 767px) {
	.seo-scroller{
		
		.seo-scroller__seo-text{
			height: 270px;
		}
	}
}

@media(max-width: 575px){
	.seo-scroller{
		padding: 20px 0 15px;
		.seo-scroller__seo-images{
			.img_row{
				.seo_img{
					height: 105px;
					margin-bottom: 10px;
					&:not(.big){
						width: calc(100% / 2 - 4px);
					}
				}
			}
			.seo_img{
				width: 100%;
				&,&.big{
					height: 130px;
				}
				&.large{
					height: 130px;
				}
				img{

				}
			}
		}
	}
}