.product-card-wide {
	$this: &;
	position: relative;
    display: flex;
    align-items: center;
    justify-content: space-between;
    min-height: 70px;
    padding: 5px;
    background-color: #fff;
    box-shadow: 0 -2px 7px rgba(0, 0, 0, 0.15);
	&:not(:last-child) {
		margin-bottom: 5px;
	}
	&:last-child {
		margin-bottom: 30px;
		border-bottom: 1px solid #e5e5e5;
	}

	&-body {
		display: flex;
		align-items: center;
		justify-content: space-between;
		width: 100%;
		height: 100%;
		position: relative;
		flex: 0 1 auto;
		padding-right: 20px;
	}

	&-thumb {
		flex: 0 0 auto;
		position: relative;
		display: flex;
		justify-content: center;
		align-items: center;
		width: 100px;
		height: 80px;
		border-radius: 4px;
		padding: 5px;
		&_no-photo {
			background-color: #dcdcdc;
		}

		&__image {
			max-width: 100%;
			max-height: 100%;
			object-fit: contain;
		}

		&__icon {
			width: 35%;
			fill: #efefef;
		}
		&:after {
			content: "";
			background-color: #eaeaea;
			position: absolute;
			left: 100%;
			width: 2px;
			top: 0;
			bottom: 0;
		}
		& + div{
			width: 100%;
			padding-left: 20px;
			padding-right: 25px;
			> div {
				display: flex;
				border-bottom: 1px solid #f1f1f1;
				align-items: center;
				&:last-child{
					border-bottom: 0
				}
			}
		}
	}
	&-preview {
		display: none;
		position: absolute;
		justify-content: center;
		align-items: center;
		z-index: 200;
		bottom: 67px;
		width: 200px;
		height: 140px;
		border-radius: 4px;
		border: 6px solid $color-general;
		background-color: #fff;

		&::before,
		&::after {
			content: "";
			position: absolute;
			left: 0;
			right: 0;
			margin: auto;
		}

		&::before {
			border: 14px solid transparent;
			border-top: 16px solid $color-general;
			bottom: -30px;
			width: 20px;
			height: 16px;
		}

		&::after {
			border: 7px solid transparent;
			border-top: 8px solid #fff;
			bottom: -14px;
			width: 14px;
			height: 6px;
		}

		&__image {
			max-width: 100%;
			max-height: 100%;
		}
	}
	&-title-wrap {
		min-height: 80px;
		display: flex;
		flex-direction: column;
		padding-top: 15px;
		align-items: flex-start;
		justify-content: space-between;
	}
	&-title {
		display: block;
		font-size: 18px;
		line-height: 24px;
		font-weight: 600;
		text-transform: capitalize;
		color: $color-text;
		span{
			display: block;
		}
	}

	&-prices {
		flex: 0 0 auto;
		display: flex;
		flex-direction: column;
		justify-content: center;
		align-items: flex-end;
		margin-right: 50px;
	}

	&-price {
		font-weight: 600;
		font-size: 16px;
		color: $color-text;

		&_sale {
			color: #d9534f;
		}
	}

	&-old-price {
		font-weight: 600;
		font-size: 13px;
		color: $color-caption;
		text-decoration: line-through;
	}

	&-no-price {
		font-weight: 600;
		font-size: 14px;
		color: $color-text;
	}

	&-form {
		position: relative;
		display: flex;
		justify-content: flex-end;
		align-items: center;
		height: 100%;
		flex: 1 0 auto;
		width: 350px;
		&:before {
			content:"";
			position: absolute;
			left: 0;
			width: 2px;
			background-color: #eaeaea;
			height: 80px;
		}
	}

	&-actions {
		display: flex;
		align-items: center;
		height: 100%;
	}

	&-action-btn {
		display: flex;
		justify-content: center;
		align-items: center;
		height: 100%;
		width: 40px;
		border: none;
		background: none;
		cursor: pointer;

		&__icon {
			width: 16px;
			height: 14px;
			fill: #dee2e6;
			transition: fill 200ms ease, transform 75ms cubic-bezier(0.175, 0.885, 0.32, 1.275);
		}

		&:hover, &.active {
			#{$this}-action-btn__icon {
				fill: $color-general;
			}
		}

		&:active {
			#{$this}-action-btn__icon {
				transform: scale(1.2);
			}
		}
	}

	&-add-cart {
		&-wrap {
			position: relative;
		}
	}

	&:hover & {
		&-title {
			// color: $color-general;
		}
	}

	&:after {
		content: "";
		position: absolute;
		bottom: 0;
		left: 0;
		right: 0;
		height: 3px;
		background-color: transparent;
		transition: .3s;
	}

	&:hover {
		&:after {
			background-color:  $color-red;
		}
		// .amount {
		// 	opacity: 1;
		// }

		// .wrapper-marker {
		// 	opacity: 0;
		// }
	}

	.amount {
		position: absolute;
		top: 0;
		bottom: 0;
		height: 30px;
		right: 5px;
		opacity: 0;
		transition: 200ms;
		margin: auto;
	}

	.badges {
		display: flex;
		flex-direction: column;
		align-items: flex-end;
		justify-content: center;
		flex-wrap: wrap;
	}

	.loading-blocks &:after {
		content: "";
		position: absolute;
		z-index: 99;
		top: -1px;
		left: -1px;
		height: calc(100% + 2px);
		width: calc(100% + 2px);
		background-image: url("../images/card_horizontal.jpg");
		background-size: 100% 100%;
	}
}

.product-card-add_inverse {
    height: 34px;
    border-radius: 25px;
    border: 1px solid $color-red;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    justify-content: center;
	padding: 0 20px;
	color: $color-red;
	font-size: 14px;
	font-weight: 600;
	&:hover {
		background-color: $color-red;
		color: #fff;
	}
}

.go-to-cart {
	position: absolute;
	z-index: 30;
	top: calc(100% + 20px);
	right: 0;
	display: none;
	flex-direction: column;
	justify-content: center;
	align-items: center;
	width: 210px;
	height: 90px;
	border-radius: 4px;
	background-color: #363a3b;

	&:before {
		content: "";
		position: absolute;
		border: 4px solid transparent;
		border-bottom: 5px solid #363a3b;
		right: 68px;
		bottom: 100%;
	}

	span {
		display: block;
		color: #fff;
		font-size: 14px;
		font-weight: 500;
		line-height: 18px;
	}

	a {
		margin: 10px 0 0;
		width: 174px;
		height: 30px;
		font-size: 12px;
	}

	.added &, .added + & {
		display: flex;
	}
}

.product-status {
    font-size: 11px;
    font-weight: 400;
    line-height: 14px;
	padding-left: 7px;
	position: relative;
	&:after {
		content: "";
		position: absolute;
		left: 0;
		width: 2px;
		height: 2px;
		border-radius: 50%;
		top: 0;
		bottom: 0;
		margin: auto;
	}
	&.not {
		color: #959595;
		&:after {
			background-color: #959595;
		}
	}
	&.yes {
		color: #2ebf9e;
		&:after {
			background-color: #2ebf9e;
		}
	}
}

.badge {
	color: #fff;
	font-size: 8px;
	font-weight: 700;
	line-height: 11px;
	text-transform: uppercase;
	border: none;
	margin: 0 0 2px 0;
	border-radius: 25px;
	transition: border-color 200ms ease, background-color 200ms ease;
	padding: 3px 5px;
	&.badge-hit {
		background-color: #4a90e2;
	}
	&.badge-new {
		background-color: #2ebf9e;
	}
	&.badge-free-montage {
		background-color: #f7ce3b;
	}
}

.product-card-wide-add-cart {
	width: 160px;
	max-width: 100%;
    height: 34px;
    border-radius: 25px;
    border: 1px solid $color-red;;
    background-color: #ffffff;
    display: flex;
    align-items: center;
    justify-content: center;
	padding: 0;
	color: $color-red;
	font-size: 14px;
    font-weight: 600;
	&__icon {
		& > svg {
			fill: #2d303f;
			margin-right: 7px;
		}
	}
	&__text {
		color: $color-red;
		font-size: 14px;
		font-weight: 600;
	}
	&:hover {
		background-color: $color-red;
		border-color: $color-red;
		color: #fff;;
		.product-card-wide-add-cart__icon {
			& > svg {
				fill: #fff;
			}
		}
		.product-card-wide-add-cart__text {
			color: #fff;
		}
	}
}

.wrapper-marker {
	flex: 0 1 auto;
	display: flex;
	.product-card-params {
		position: static;
		margin-right: 40px;
	}
}


@media (min-width: 1200px) {
	.product-card-wide-thumb {
		&:hover {
			.product-card-wide-preview {
				display: flex;
			}
		}
	}
}

@media (max-width: 1199px) {
	.go-to-cart{
		display: none !important;
	}
	.product-card-wide{

	}
}

@media (max-width: 991px) {
	.wrapper-marker .product-card-params {
		margin-right: 10px;
	}
	.product-card-wide-prices {
		margin-right: 20px;
	}
	.product-card-wide-form {
		width: 300px;
	}
}

@media (max-width: 768px) {



	.product-card-wide {
		flex-direction: column;
		align-items: flex-start;
		height: auto;
		padding: 10px 0 5px;


		&-thumb {
			&:after {
				display: none;
			}
			& + div {
				padding-left: 10px;
				padding-right: 10px;
				& > div {
					flex-direction: column;
				}
			}
		}

		&-body {
			flex-direction: column;
			align-items: flex-start;
			width: 100%;
			padding: 0;
			position: static;
		}

		&-preview {
			display: none;
		}

		&-title {
			margin: 6px 0;
		}

		&-prices {
			align-items: flex-start;
			margin-right: 0;
		}

		&-form {
			flex-direction: row;
			justify-content: space-between;
			width: 100%;
			padding: 0;
			margin: 0;
			&:before {
				display: none;
			}
		}
		&-no-price {
			font-size: 12px;
		}
		&-price {
			font-size: 12px;
		}
		
		&-actions {
			flex-direction: row-reverse;
		}
		&-title-wrap {
			height: auto;
			min-height: auto;
			padding-bottom: 10px;
		}
		&-title {
			font-size: 14px;
			line-height: 16px;
			margin-top: 0;
		}
		&-action-btn {
			width: 30px;
			height: 30px;
		}
		&-add-cart{
			margin-left: 0;
			width: 130px;
		}
		&-cart {
			margin-left: 0;
			margin-right: 0;
		}

		&:hover {
			.badges {
				opacity: 1;
			}
		}

		.amount {
			position: absolute;
			opacity: 1;
			margin: 6px 0;
			top: 60px;
			right: 10px;
		}



		.badge {
			margin: 2px 5px 2px 0;
		}
	}
	.wrapper-marker {
		position: absolute;
		right: 0;
		top: 10px;
		opacity: 1 !important;
	}
}