.extra-products-slider{
	padding:50px 0 30px;
	&.active &{
		&__slider-header{
			.nav-buttons{
				display: flex;
			}
		}
	}
	&__slider-header{
		display: flex;
		align-items: flex-end;
		justify-content: space-between;
		margin-bottom: 30px;
		&-title{
			display: block;
			color: #2d303f;
			font-size: 24px;
			font-weight: 700;
			line-height: 30px;
			text-transform: uppercase;
			position: relative;
			&:after{
				content: "";
				position: absolute;
				top: 0;
				bottom: 0;
				left: calc(100% + 7px);
				width: 178px;
				background-image: url('/static/images/wheel-protector.png');
				background-position: center;
				background-repeat: no-repeat;
				background-size: contain;
			}
		}
		.nav-buttons{
			display: none;
			justify-content: space-between;
			width: 75px;
			a{
				display: flex;
				justify-content: center;
				align-items: center;
				width: 35px;
				height: 35px;
				border-radius: 4px;
				border: 1px solid #e5e5e5;
				background-color: #fff;
				transition: .3s;
				&:hover{
					border-color: $color-general;
					> svg{
						fill: $color-general;
					}
				}
				&.slick-hidden{
					display: none;
				}
				&.next{
					> svg{
						transform: rotate(180deg);
					}
				}
				> svg{
					fill: #9b9b9b;
					transition: .3s;
				}
			}
		}
	}
	&__slider-body{
		overflow: hidden;
		.slick-list{
			margin: 0 -15px;
			overflow-y: visible;
			overflow-x: hidden;
			.slick-track{
				min-width: 100%;
			}
		}
		.slick-slide{
			margin: 0 15px;
		}
	}
	&__no-slider{
		padding: 15px 0;
	}
}

.button-all-product {
	$this:&;
    width: 224px;
    height: 34px;
    border-radius: 21px;
    border: 1px solid #cecece;
    display: flex;
    align-items: center;
	justify-content: center;
	transition:.2s;
	&__text {
		color: #2d303f;
		font-size: 14px;
		font-weight: 400;
		transition:.2s;
	}
	&:hover {
		background-color: $color-red;
		border-color: $color-red;
		#{ $this }__text {
			color: #fff;
		}
	}
}

@media(max-width: 767px) {
	.extra-products-slider {
		&__slider-header-title {
			font-size: 20px;
			line-height: 26px;
			&:after {
				width: 130px;
			}
		}
	}
	.button-all-product {
		width: 180px;
		height: 26px;
	}
}

@media(max-width: 575px){
	.extra-products-slider{
		padding: 15px 0;
		&__slider-header{
			flex-direction: column;
			align-items: flex-start;
			&-title{
				font-size: 16px;
				margin-bottom: 10px;
				line-height: 24px;
			}
		}
	}
}