.theme-button{
	text-decoration: none;
	display: flex;
	justify-content: center;
	align-items: center;
	height: 40px;
	padding: 0 15px;
	margin-right: 5px;
	border-radius: 30px;
	border: 1px solid transparent;
	color: #fff;
	font-size: 16px;
	font-weight: 400;
	line-height: 21px;
	transition: .3s;
	&:hover{
		border-color: #eceeef;
	}
	&.theme-button{
		text-decoration: none;
		display: flex;
		justify-content: center;
		align-items: center;
		height: 40px;
		padding: 0 15px;
		margin-right: 5px;
		border-radius: 30px;
		border: 1px solid $color-red;
		color: $color-red;
		font-size: 16px;
		font-weight: 400;
		line-height: 21px;
		transition: .3s;
		&:hover{
			color: #fff;
			background-color: $color-red;
		}
	}
	.theme-button_active{
		background-color: $color-red;
		border-color: $color-red;
		color: #fff;
	}
	&_active{
		background-color: $color-red;
		border-color: $color-red;
		color: #fff;
	}
}

.nav-tabs .nav-link {
	border-radius: 0 !important;
	&:hover {
		background-color: #2d303f;
		border-bottom-color: #2d303f;
		color: #fff;
	}
}