.mobile-menu{
	border-top: 1px solid rgba(216, 216, 216, 0.16);
	&, & .sub-menu{
		position: fixed;
		z-index: 1005;
		left: 0;
		top: 60px;
		width: 100%;
		height: calc(100% - 60px);
		overflow-y: scroll;
		background-color: $color-blue-dark;
		left: 110%;
		transition: .3s;
		&.opened{
			left: 0;
		}
		&.no-scroll{
			overflow: hidden;
		}
	}

	&__catalog{
		padding: 0 10px;
		&-link{
			display: flex;
			padding: 13px 0;
			color: #fff;
			font-size: 16px;
			font-weight: 600;
			border-bottom: 1px solid rgba(216, 216, 216, 0.16);
			transition: .3s;
			&:hover{
				color: $color-red;
			}
			
			&-icon{
				display: flex;
				align-items: center;
				justify-content: center;
				width: 45px;
				height: 100%;
				> svg{
					fill: $color-red;
				}
			}
		}
	}
	&__search{
		position: relative;
		height: 50px;
		margin: 0 10px;
		border-bottom: 1px solid rgba(216, 216, 216, 0.16);
		input{
			width: 100%;
			height: 100%;
			padding: 0 35px 0 10px;
			background-color: transparent;
			border: none;
			color: #fff;
			font-size: 14px;
			font-weight: 400;
		}
		button{
			display: flex;
			justify-content: center;
			align-items: center;
			position: absolute;
			top: 0;
			right: 0;
			width: 35px;
			height: 100%;
			border: none;
			background: transparent;
			cursor: pointer;
			&:hover{
				> svg{
					fill: $color-red;
				}
			}
			> svg{
				fill: rgba(255, 255, 255, 0.38);
				transition: .3s;
			}
		}
	}
	&__main-menu{
		padding: 15px 0;
		&-link{
			display: block;
			padding: 10px 20px;
			color: #fff;
			font-size: 14px;
			font-weight: 400;
			transition: .3s;
			&:hover{
				color: $color-red;
			}
		}
	}
	&__callback{
		display: flex;
		align-items: center;
		justify-content: center;
		width: 280px;
		max-width: 100%;
		height: 35px;
    	margin: 15px 20px 0;
		border-radius: 22px;
		border: 1px solid $color-red;
		transition: .3s;
		$button: &;
		&:hover{
			background-color: $color-red;
			#{$button}-icon{
				> svg{
					fill: #fff;
				}
			}
			#{$button}-text{
				color: #fff;
			}
		}
		&-icon{
			> svg{
				fill: $color-red;
				margin-right: 10px;
			}
		}
		&-text{
			color: $color-red;
			font-size: 15px;
			font-weight: 600;
		}
	}
}