.filter-block{
	border-bottom: 1px solid #e5e5e5;
	&__header{
		display: block;
		position: relative;
		background-color: #fff;
		padding: 12px 13px;
		color: #373a3c;
		border-bottom: 1px solid #e5e5e5;
		font-size: 12px;
		font-weight: 700;
		line-height: 16px;
		transition: .3s;
		&:hover{
			color: $color-red;
		}
		&.collapsed{
			border-bottom-color:transparent;
			&:after{
				transform: rotate(0deg);
			}
		}
		&:after{
			content: "";
			position: absolute;
			right: 12px;
			top: 0;
			bottom: 0;
			margin: auto;
			display: inline-block;
			width: 0;
			height: 0;
			border-top: 4px solid;
			border-right: 4px solid transparent;
			border-bottom: 0;
			border-left: 4px solid transparent;
			transform: rotate(180deg);
			transition: transform .3s linear, -webkit-transform .3s linear;
		}
	}
	&__body{
		.custom-control{
			padding: 3px 0 3px 24px;
			&-label{
				&:before, &:after{
					top: 3px;
				}
			}
			input{
				display: none;
			}
		}
	}
	&__wrapper{
		padding: 8px 13px;
		overflow: hidden;
	}
	&__scroll-content{
		max-height: 210px;
		.os-content{
			padding: 0 15px 0 0 !important;
		}
	}
	&__search_field{
		margin-bottom: 14px;
	}
}

.disabled_class {
    color: #bdbdbd !important;
    pointer-events: none;
}

.custom-control-label {
	font-size: 14px;
    font-weight: 600;
	line-height: 18px;
	text-transform: capitalize;
	&:after {
		border: 1px solid #cccccc;
		background-color: #fff;
	}
}

.custom-checkbox .custom-control-input:checked ~ .custom-control-label::after {
	border: 0px solid $color-red;
	background-color: $color-red;
}
.custom-control-label::before {
	border-radius: 0;
}
.custom-checkbox .custom-control-input:checked ~ .custom-control-label::before {
	background-color: $color-red;
}

.custom-checkbox {
	&:hover {
		.custom-control-label {
			color: $color-red;
			&:after {
				border-color: $color-red;
			}
		}
	}
}

.reset_filter {
    color: #373a3c;
    font-size: 12px;
    font-weight: 400;
	line-height: 13px;
	width: calc(50% - 5px);
	&:hover{
		color: $color-red;
		border-color: $color-red;
	}
}

.result-btn {
	background-color: $color-red;
	border-color: $color-red;
	&:hover {
		background-color: #fff;
		color: $color-red;
		border-color: $color-red;
	}
}