
.products-wrapper {
    display: flex;
    flex-wrap: wrap;
    width: 100%;
    align-items: flex-start;
    justify-content: flex-start;
    padding-bottom: 10px;
}

.page .product-card {
	width: calc(20% - 20px);
}

@media(max-width: 1400px) {
	.products-wrapper.onegg{
		.product-card:nth-child(n + 6){
			display: none;
		}
	}
}

@media(max-width: 991px) {
	.products-wrapper.onegg{
		.product-card:nth-child(n + 5){
			display: none;
		}
	}
}

@media(max-width: 767px) {
	.products-wrapper.onegg{
		.product-card:nth-child(n + 5){
			display: block;
		}
	}
}

.product-card {
    $this: &;
    position: relative;
    display: block;
    width: calc(16.666666666666668% - 20px);
    margin: 0 10px 20px;
    height: 315px;
	box-shadow: 0 2px 7px rgba(0, 0, 0, 0.15);
	background-color: #fff;

	&:hover{
		#{$this}__to-product{
			&:after{
				opacity: 1;
			}
			&-icon{
				> svg{
					fill: #fff;
				}
			}
		}

	}

    &-thumb {
        flex: 0 0 auto;
        position: relative;
        display: flex;
        justify-content: center;
        align-items: center;
        height: 175px;
        padding: 12px 15px;

        &::after {
            content: "";
            position: absolute;
            top: 0;
            left: 0;
            width: 100%;
            height: 100%;
            background-color: rgba($color-general, 0.85);
            opacity: 0;
            transition: opacity 200ms ease;
        }

        &__image {
            max-width: 100%;
            max-height: 100%;
        }

        &__icon {
            width: 50px;
            height: 50px;
            fill: #dbdbdb;
        }
	}
	
	&-params {
		display: block;
        position: absolute;
        z-index: 5;
		top: 10px;
		left: 10px;
	}
	
	&-param{
		display: flex;
		align-items: center;
		justify-content: center;
		width: 30px;
		height: 30px;
		> svg{
			max-width: 100%;
			max-height: 100%;
		}
	}

    &-badges {
		display: flex;
		flex-direction: column;
		align-items: flex-end;
        position: absolute;
        z-index: 5;
        top: 10px;
        right: 12px;
    }


	&-body{
		display: block;
		position: relative;

		&:before{
			content: "";
			position: absolute;
			top: 0;
			left: 10px;
			width: calc(100% - 20px);
			height: 1px;
			background-color: #eaeaea;
		}
	}

	&-main{
		display: flex;
		flex-direction: column;
		justify-content: space-between;
		height: 110px;
		padding: 10px 15px 15px;

		
	}
    &-title {
		display: block;
		height: 48px;
        color: $color-blue-dark;
		font-size: 18px;
		line-height: 24px;
		font-weight: 600;
		transition: color 200ms ease;
		overflow: hidden;
		text-transform: capitalize;
	}
	&__to-product{
		display: flex;
		align-items: center;
		justify-content: space-between;
		position: relative;
		width: 145px;
		height: 34px;
		padding: 0 16px 0 12px;
		border-radius: 25px;
		border: 1px solid $color-red;
		background-color: #fff;
		overflow: hidden;
		
		&:after{
			content: "Подробнее";
			display: flex;
			align-items: center;
			position: absolute;
			border-radius: 25px;
			z-index: 2;
			top: 0;
			left: 0;
			padding: 0 10px;
			width: 100%;
			height: 100%;
			color: #fff;
			font-size: 14px;
			font-weight: 600;
			background-color: $color-red;
			opacity: 0;
			transition: opacity .2s linear;
		}

		&-price{
			display: block;
			color: $color-red;
			font-size: 16px;
			font-weight: 600;
		}
		&-icon{
			display: block;
			position: relative;
			z-index: 3;
			> svg{
				fill: $color-blue-dark;
				transition: fill .2s linear;
			}
		}
	}
	&-footer{
		display: block;
		padding: 0 10px;
	}
	&-status{
		display: block;
		position: relative;
		padding-left: 12px;
		color: #959595;
		font-size: 11px;
		font-weight: 400;
		line-height: 18px;
		&:before{
			content: "";
			position: absolute;
			left: 4px;
			top: 7px;
			width: 4px;
			height: 4px;
			border-radius: 100%;
			background-color: #959595;
		}
		&_available{
			color: #2ebf9e;
			&:before{
				background-color: #2ebf9e;
			}
		}
	}

    .loading-blocks &:after {
        content: "";
        position: absolute;
        z-index: 99;
        top: -1px;
        left: -1px;
        height: calc(100% + 2px);
        width: calc(100% + 2px);
        background-image: url("../images/card_big.jpg");
        background-size: 100% 100%;
    }
}

@media(max-width: 1400px) {
	.page .product-card {
		width: calc(25% - 20px);
	}
	.product-card {
		width: calc(20% - 20px);
	}
}

@media(max-width: 1200px) {
	.page .product-card {
		width: calc(20% - 20px);
	}
}

@media(max-width: 991px) {
	.page .product-card,
	.product-card {
		width: calc(25% - 20px);
	}
}

@media(max-width: 767px) {
	.page .product-card,
	.product-card {
		width: calc(33.3334% - 20px);
	}
}

@media(max-width: 575px){

	.page .product-card {
		width: calc(100% - 20px);
	}
	.product-card{
		display: flex;
		flex-direction: row;
		height: auto;
		width: calc(100% - 20px);
		margin: 0 auto 10px;

		&-thumb{
			width: 104px;
			height: 100%;
			padding: 15px 20px;
		}
		&-body{
			display: flex;
			flex-direction: column-reverse;
			justify-content: flex-end;
			padding: 12px 15px;
			&:before{
				left: 0;
				top: 10px;
				height: calc(100% - 20px);
				width: 1px;
			}
		}
		&-main{
			padding: 0;
			height: auto;
		}
		&-footer{
			padding: 0 0 5px;
		}
		&-title{
			height: 32px;
			font-size: 11px;
			line-height: 16px;
		}
		&__to-product{
			margin: 10px 0 0;
		}
		&-params{
			top: 7px;
			left: 5px;
		}
		&-param{
			width: 18px;
			height: 18px;
			margin: 0 0 2px;
		}
		&-badges{
			top: 9px;
			right: 5px;
		}
		&-badge{
			padding: .35em .45em;
			font-size: 5px;
		}
	}
}

