.index-best-products{
	padding: 72px 0 65px;
	overflow: hidden;

	&__title{
		display: block;
		padding: 0 0 50px;
		text-align: center;
		&-text{
			display: inline-flex;
			position: relative;
			color: #2d303f;
			font-size: 36px;
			font-weight: 700;
			text-transform: uppercase;
			&:before, &:after{
				content: "";
				position: absolute;
				top: 0;
				width: 345px;
				height: 65px;
				background-image: url('/static/images/wheel-protector.png');
				background-position: center;
				background-repeat: no-repeat;
			}
			&:before{
				right: calc(100% + 25px);
			}
			&:after{
				left: calc(100% + 35px);
			}
		}
	}
	&__button-all{
		display: flex;
		align-items: center;
		justify-content: center;
		width: 330px;
		height: 50px;
		margin: 30px auto 0;
		max-width: 100%;
		border-radius: 25px;
		border: 1px solid #dfdfdf;
		color: $color-red;
		font-size: 15px;
		font-weight: 600;
		transition: .3s;
		&:hover{
			color: #fff;
			border-color: $color-red;
			background-color: $color-red;
		}
	}
}

@media(max-width: 767px){
	.index-best-products{
		padding: 35px 0;
		&__title{
			&-text{
				font-size: 30px;
				&:before, &:after{
					height: 100%;
				}
			}
		}
	}
}

@media(max-width: 575px){
	.index-best-products{
		padding: 28px 0;
		
		&__title{
			padding: 0 0 5px;
			&-text{
				font-size: 18px;
				&:before{
					left: 0;
					right: 0;
					top: -30px;
					margin: auto;
					width: 140px;
					height: 30px;
					background-size: contain;
				}
				&:after{
					display: none;
				}
			}
		}

		&__button-all{
			height: 35px;
			margin: 15px auto 0;
		}
	}
}