.sidebar {
    $this: &;
    background-color: #fff;
    margin: 0 0 30px;

    &-item {
        display: block;
		position: relative;
        &:hover {
            #{$this}-link {
                background-color: #f5f5f5;
            }
        }

        &.current-menu-item {
            #{$this}-link {
				padding-left: 30px;
				&:after {
					width: 15px;
				}
            }
        }
		&:not(:last-child) {
			&:after {
				content: "";
				position: absolute;
				bottom: 0;
				left: 0;
				width: 100%;
				height: 1px;
				background-color: #e6e6e6;
			}
		}
    }

    &-link {
        display: block;
        display: flex;
        align-items: center;
        position: relative;
        color: #373a3c;
        font-size: 16px;
        font-weight: 400;
        padding: 12px 20px;
        transition: color 200ms ease, background-color 200ms ease;
		&:after {
			content: "";
			position: absolute;
			left: 0;
			top: 0;
			bottom: 0;
			margin: auto;
			width: 0;
			height: 2px;
			background-color: $color-red;
		}
    }
}