.custom-selector{
	display: block;
	position: relative;

	&:after{
		content: "";
		position: absolute;
		top: 0;
		bottom: 0;
		right: 20px;
		margin: auto;
		width: 10px;
		height: 10px;
		background-image: url('data:image/png;base64,iVBORw0KGgoAAAANSUhEUgAAAAoAAAAGBAMAAAAI3hL1AAAAAXNSR0IB2cksfwAAABVQTFRFAAAAr6+vs7Ozr6+vs7OzsrKysrKy6hfHBAAAAAd0Uk5TACBQMED/YDT8ye4AAAApSURBVHicYxBiYGBQZnBNYGALYmALY0hNYGBITQsDCrKFApkMDEoMDABQVQRSUJk3cAAAAABJRU5ErkJggg==');
		background-repeat: no-repeat;
		background-position: center;
	}

	&__select{
		display: block;
		width: 100%;
		height: 100%;
	    padding: 0 40px 0 18px;
		border-radius: 25px;
		border: 1px solid #e3e3e3;
		background-color: #fff;
		outline: none !important;
		box-shadow: none !important;
		-webkit-appearance: none;
		-moz-appearance: none;
		-ms-appearance: none;
		appearance: none !important;
		color: #333;
		font-size: 15px;
		font-weight: 400;
	}
}