.sidebar-helper{
	display: block;
	position: relative;
	background-size: cover;
	background-repeat: no-repeat;
	background-position: center;
	height: 320px;
	max-width: 270px;
	margin: 0 0 30px;
	&:after{
		content: "";
		position: absolute;
		top: 0;
		left: 0;
		width: 100%;
		height: 100%;
		background-color: rgba(#2d303f, 0.75);
	}
	&__wrapper{
		position: absolute;
		z-index: 2;
		max-width: calc(100% - 32px);
		bottom: 34px;
		left: 16px;
	}
	&__title{
		display: block;
		color: #ffffff;
		font-size: 18px;
		font-weight: 700;
		line-height: 23px;
	}
	&__button{
		display: inline-block;
		padding: 7px 16px;
		margin: 20px 0 0;
		line-height: 18px;
		color: #ffffff;
		font-size: 14px;
		font-weight: 600;
		transition: .3s;
		border-radius: 19px;
		background-color: $color-red;
		border: 1px solid $color-red;
		&:hover{
			background-color: #fff;
			color: $color-red;
		}
	}
}