.cart-button{
    display: flex;
	align-items: center;
	justify-content: center;
	height: 65px;
	flex: 0 0 80px;
	padding: 10px 13px;
	background-color: $color-red;
    transition: background-color 200ms ease;
	$button: &;

    &__icon {
        fill: #fff;
		transition: fill 200ms ease;
    }

    &__count {
        display: inline-flex;
        justify-content: center;
        align-items: center;
		transform: scale(0);
		
        color: #fff;
		font-size: 17px;
		font-weight: 400;
        transition: color 200ms ease, margin-left 200ms ease 100ms, transform 200ms ease 100ms;

        &.filled {
            margin-left: 10px;
            transform: scale(1);
            transition: color 200ms ease, margin-left 200ms ease, transform 200ms ease;
        }
    }

    &:hover {
		background-color: #fff;
		#{$button}__icon{
			fill: $color-red;
		}
		#{$button}__count{
			color: $color-red;
		}
    }
}