.sort-parameters{
	display: flex;
	align-items: center;
	justify-content: space-between;
	&__order-by{
		color: #999;
		font-size: 14px;
		font-weight: 400;
		line-height: 22px;
		margin: 20px 30px;
		select{
			border: none;
			outline: none;
			color: #999;
			font-size: 15px;
			line-height: 21px;
			appearance: none;
			background: transparent;
		}
		span {
			padding: 0 10px;
			cursor: pointer;
			transition: .2s;
			&:hover {
				color: #ff2a2a;
			}
			&.active {
				color: #ff2a2a;
			}
			code {
				font-size: 9px;
				position: relative;
				top: -3px;
				color: inherit;
			}
		}
	}
	&__layout-types{
		display: flex;
		margin-right: 30px;
		button{
			display: flex;
			justify-content: center;
			align-items: center;
			width: 30px;
			height: 30px;
			margin: 0 0 0 5px;
			border-radius: 4px;
			border: 1px solid #e5e5e5;
			background-color: #fff;
			outline: none;
			transition: .3s;
			cursor: pointer;
			&.active{
				border-color: $color-red;
				> svg{
					fill: $color-red;
				}
			}
			> svg{
				transition: .3s;
				fill: #999;
			}
		}
	}

}
.checked-label {
	color: #fff !important
}
.checked-svg {
	fill: #fff !important;
}
.checked-filter-item {
	background-color: #ff2b2b !important;
}