.search-form {
    flex: 1 0 auto;
	margin: 0 15px;
	height: 40px;
	position: relative;
	input{
		width: 100%;
		height: 100%;
		border-radius: 4px;
		border: 1px solid #ccc;
		background-color: #fff;
		padding: 0 40px 0 15px;
	}
	button{
		display: flex;
		justify-content: center;
		align-items: center;
		position: absolute;
		top: 0;
		right: 0;
		width: 35px;
		height: 100%;
		border: none;
		background: transparent;
		cursor: pointer;

		> svg{
			fill: $color-general;
		}
	}
	&__ajax-search{
		padding: 10px 20px 0;
		
		> ul{
			display: none;
			border-radius: 4px;
			border: 1px solid #ccc;
			background-color: #fff;
			position: absolute;
			z-index: 200;
			left: 0;
			top: 100%;
			width: 100%;
			max-height: 500px;
			overflow-y: scroll;
			li{
				a, span{
					display: block;
					padding: 8px 20px;
					color: #9b9b9b;
					font-size: 15px;
					font-weight: 600;
					line-height: 21px;
					background-color: #f5f5f5;
					transition: .2s;
					&:hover{
						color: $color-general;
					}
				}
			}

			ul{
				padding: 2px 0;
				li{
					a{
						
						padding: 5px 20px;
						color: #373a3c;
						font-size: 16px;
						font-weight: 400;
						line-height: 22px;
						background-color: transparent;
						&:hover{color: $color-general;}
					}
				}
			}
		}
	}
}
