@import url(https://fonts.googleapis.com/css?family=Montserrat:400,600,700&subset=cyrillic,cyrillic-ext);


* {
    margin: 0;
    padding: 0;
    box-sizing: border-box;
}

html {
    min-height: 100%;
	height: 100%;
	&.opened{
		overflow: hidden;
		height: 100%;
		position: fixed;
	}
}

body {
    min-width: 320px;
	height: 100%;
	width: 100%;
    margin: 0;
    padding: 0;
	font-family: 'Montserrat', sans-serif;
	background-color: #fefce8;
	&.opened {
		overflow: hidden;
		height: 100%;
		position: fixed;
	}
}

::-webkit-input-placeholder { color: $color-placeholder; }
::-moz-placeholder          { color: $color-placeholder; }
:-moz-placeholder           { color: $color-placeholder; }
:-ms-input-placeholder      { color: $color-placeholder; }

ol, ul {
    padding: 0;
    margin: 0;
}

li {
    list-style-type: none;
}

.h1, .h2, .h3, .h4, h1, h2, h3, h4, p {
    line-height: normal;
    margin: 0;
    padding: 0;
}

a {
    color: $color-text;
    text-decoration: none;
    &:hover {
        color: $color-general;
        text-decoration: none;
    }
}

a:focus, button:focus, input:focus, textarea:focus {
    outline: 0;
    text-decoration: none;
}

// Базовая обертка сайта
.base {
    &-wrapper, &-container {
        width: 100%;
    }
    
    &-wrapper {
        display: flex;
        flex-direction: column;
        min-height: 100%;
    }

    &-container {
        flex: 1  auto;
        overflow: initial;
        padding: {
            top: 150px;
        }
    }

    &-page {
        padding-bottom: 90px;
    }
}

.btn:focus {
    -webkit-box-shadow: none !important;
    box-shadow: none !important;
}

.navbar {
    padding: 0;
}

.params-item~.params-delimeter {
    display: none;
}

.params-delimeter::before {
    content: '-';
}



.mobile-search-trigger{
	svg{
		pointer-events: none;
	}
}
.nav-close{
	*{
		pointer-events: none;
	}
}
.container{
	padding-right: 10px;
    padding-left: 10px;
}
.row{
	margin-right: -10px;
    margin-left: -10px;
}
.col-1, .col-2, .col-3, .col-4, .col-5, .col-6, .col-7, .col-8, .col-9, .col-10, .col-11, .col-12, .col, .col-auto, .col-sm-1, .col-sm-2, .col-sm-3, .col-sm-4, .col-sm-5, .col-sm-6, .col-sm-7, .col-sm-8, .col-sm-9, .col-sm-10, .col-sm-11, .col-sm-12, .col-sm, .col-sm-auto, .col-md-1, .col-md-2, .col-md-3, .col-md-4, .col-md-5, .col-md-6, .col-md-7, .col-md-8, .col-md-9, .col-md-10, .col-md-11, .col-md-12, .col-md, .col-md-auto, .col-lg-1, .col-lg-2, .col-lg-3, .col-lg-4, .col-lg-5, .col-lg-6, .col-lg-7, .col-lg-8, .col-lg-9, .col-lg-10, .col-lg-11, .col-lg-12, .col-lg, .col-lg-auto, .col-xl-1, .col-xl-2, .col-xl-3, .col-xl-4, .col-xl-5, .col-xl-6, .col-xl-7, .col-xl-8, .col-xl-9, .col-xl-10, .col-xl-11, .col-xl-12, .col-xl, .col-xl-auto{
	padding-right: 10px;
    padding-left: 10px;
}




@media (min-width: 1464px) {
    .container {
        max-width: 1464px;
	}
	.col-xxl-2{
		flex: 0 0 16.66667%;
		max-width: 16.66667%;
	}
	.col-xxl-3{
		flex: 0 0 25%;
		max-width: 25%;
	}
	.col-xxl-4{
		flex: 0 0 25%;
		max-width: 25%;
	}

	.col-xxl-6{
		flex: 0 0 50%;
		max-width: 50%;
	}

	.col-xxl-12{
		flex: 0 0 100%;
		max-width: 100%;
	}
}

@media (max-width: 1544px) {
    .container {
        width: 100%;
		max-width: 100%;
		padding: 0 50px;
    }
}


@media (max-width: 1199px) {
    .container {
		padding: 0 10px;
    }
	.base {
        &-container {
            padding: {
                top: 60px;
            }
        }
    }
}
