.footer {
    flex: 0 1 auto;
	background-color: $color-blue-dark;
	&-logo{
		display: block;
		padding: 35px 0;
	}
	&-block{
		padding: 50px 0 20px 20px;
		&__title{
			display: block;
			position: relative;
			padding: 0 0 25px;
			color: #fff;
			font-size: 16px;
			font-weight: 700;
			letter-spacing: 0.4px;
			text-transform: uppercase;
			&:before{
				content: "";
				position: absolute;
				left: -20px;
				top: 0;
				height: 20px;
				width: 3px;
				background-color: $color-red;
			}
		}
		&__menu{
			li{
				a{
					display: block;
					padding: 7px 0;
					color: #b8bbc9;
					font-size: 16px;
					font-weight: 400;
					line-height: 22px;
					transition: .3s;
					&:hover{
						color: $color-red;
					}
				}
			}
		}
		&__req{
			display: block;
			padding: 0 0 12px;
			&-title{
				display: block;
				color: $color-red;
				font-size: 13px;
				font-weight: 600;
				line-height: 22px;
			}
			&-text{
				display: inline-block;
				color: #b8bbc9;
				font-size: 16px;
				font-weight: 400;
				line-height: 22px;
				transition: .3s;

				&.email{
					border-bottom: 1px solid rgba(255,255,255,0.17);
				}
				&.phone, &.email{
					&:hover{
						color: $color-red;
						border-bottom-color: $color-red;
					}
				}
			}
		}
	}
	&__lower-part{
		margin-top: 23px;
		border-top: 1px solid rgba(216, 216, 216, 0.16);
		&-wrapper{
			display: flex;
			justify-content: space-between;
			flex-wrap: wrap;
			height: 100px;
		}
		&-left{
			display: flex;
			align-items: center;
			p{
				padding-right: 60px;
			}
			a{
				text-decoration: underline;
				transition: .3s;
				&:hover{
					color: $color-red;
					text-decoration: none;
				}
			}
			p, a{
				display: block;
				color: rgba(255,255,255,0.37);
				font-size: 14px;
				font-weight: 400;
				line-height: 16px;
			}
		}
	}
	&__made-by{
		display: flex;
		align-items: center;
		color: rgba(255,255,255,0.37);
		font-size: 14px;
		font-weight: 400;
		line-height: 16px;
		transition: .3s;
		> svg{
			fill: #fff;
			margin-left: 10px;
			transition: .3s;
		}
		&:hover{
			color: $color-red;
			> svg{
				fill: $color-red;
			}
		}
	}
}






@media (max-width : 991px) {
	.footer{
		&__lower-part{
			&-wrapper{
				height: auto;
				flex-direction: column;
				justify-content: center;
				padding: 10px 0;
			}
			&-left{
				flex-direction: column;
				p, a{
					padding: 4px 0;
					text-align: center;
				}
			}
		}
		
		&__made-by{
			padding: 4px 0;
			justify-content: center;
		}
	}
}

@media (max-width : 575px) {
	.footer{
		&-block{
			padding: 15px 0 0 20px;
			&__title{
				padding: 0 0 12px;
			}
		}
		&__lower-part{
			margin-top: 14px;
		}
		&__made-by{
			padding: 25px 0;
			> svg{
				width: 107px;
				height: 22px;
			}
		}
	}
}