.cart {
    &-table {
        border: 1px solid #DDDDDD;
        border-radius: 4px;
        overflow: hidden;

        &-head {
            display: flex;
            align-items: center;
            height: 30px;
            background-color: #eceeef;
            border-bottom: 1px solid $color-border;
			padding-left: 20px;
			.cart-table-row {
				background-color: transparent;
			}

            &__title {
				color: #999999;
				font-size: 12px;
				font-weight: 400;
				line-height: 16px;
            }
        }

        &-row {
            display: flex;
            width: 100%;
			height: 100%;
			background-color: #fff;
        }

        &-col {
            display: flex;
            flex-direction: column;
            justify-content: center;
            align-items: flex-start;
            height: 100%;
			&_product {
				width: 100%;
				flex: 0 1 auto;
			}
			&_term {
				width: 150px;
				flex: 0 0 auto;
			}
            &_price {
                flex: 0 0 auto;
                width: 150px;
            }

            &_count {
                flex: 0 0 auto;
                width: 150px;
            }

            &_total {
                flex: 0 0 auto;
                width: 150px;
            }

            &_delete {
                flex: 0 0 auto;
                width: 35px;
            }
        }
    }

    &-desc {
        font-weight: 400;
        font-size: 15px;
        line-height: 20px;
        color: $color-caption;
        border: 1px dashed $color-border;
        border-radius: 4px;
        padding: 12px 20px;
        margin-top: 20px;
    }

    &-order {
        display: flex;
        justify-content: flex-end;
        align-items: flex-end;
        margin-top: 30px;

        &-btn {
            min-width: 270px;
			margin-left: 30px;
			background-color: $color-red;
			border-color: $color-red;
			&:hover, &:focus, &:active {
				color: $color-red !important;
				background-color: #fff !important;
				border-color: $color-red !important;
			}
        }
    }

    &-text {
        display: inline-flex;
        align-items: flex-end;
        font-weight: 500;
        color: $color-text;
    }

    &-total, &-currency {
        font-weight: 700;
        font-size: 30px;
        margin-left: 5px;
        margin-bottom: -2px;
    }
}

.btn-primary {
    color: #fff;
	background-color: $color-red;
	border-color: $color-red;
	&:hover, &:focus, &:active {
		color: $color-red !important;
		background-color: #fff !important;
		border-color: $color-red !important;
	}
}

@media(max-width: 1199px) {
    .cart {
        &-order {
            flex-direction: column;
            justify-content: flex-start;
            align-items: flex-start;
            margin-top: 20px;

            &-btn {
                margin-top: 10px;
                margin-left: 0;
            }
        }
    }
}

@media(max-width: 991px) {
    .cart {
        &-table {
            &-head {
                display: none;
            }

            &-row {
				flex-wrap: wrap;
				position: relative;
				padding: 20px 15px 10px;
            }

            &-col {
                width: 100%;
                height: auto;
                padding:0;
				&_term {
					padding-left: 60px;
					margin-top: -20px;
				}
				&_price {
					padding-left: 60px;
					padding-bottom: 10px;
				}
				&_count {
					width: 50%;
				}
				&_total {
					width: 50%;
					display: flex;
					align-items: flex-end;
				}

				&_delete {
					padding: 0;
				}
			}
        }
    }
}
