.page-title {
    display: block;
    line-height: 44px;
    margin-bottom: 26px;
    color: #2d303f;
    font-family: Montserrat;
    font-size: 36px;
    font-weight: 700;
    //text-transform: uppercase;
    
    &_small {
        font-size: 20px;
        line-height: 22px;
        padding: 0 0 16px;
    }
}

@media (max-width: 565px) {
	.page-title {
		line-height: 32px;
		padding: 5px 0 15px;
        font-size: 26px;
        
        &_small {
            font-size: 18px;
            line-height: 20px;
        }
	}
}
