.range-slider-field{
	position: relative;
	&__inputs{
		display: flex;
		justify-content: space-between;
		margin: 3px 0 0px;
	}
	&__min-price, &__max-price{
		width: calc(50% - 5px) !important;
		height: 30px;
		color: #373a3c;
		font-size: 15px;
		font-weight: 400;
		line-height: 21px;
		padding: 0 16px;
		border-radius: 15px;
		border: 1px solid #cccccc;
		background-color: #ffffff;
		&::placeholder{
			color: #9b9b9b;
			font-size: 14px;
		}
		&::-webkit-outer-spin-button, &::-webkit-inner-spin-button{
			-webkit-appearance: none;
			margin: 0; 
		}
	}
	
}



.irs{
	height: 40px;
	&-min, &-max, &-from, &-to, &-single{
		display: none;
	}
	&-line{
		top: 22px;
		border-radius: 2px;
		background: #e5e5e5;
		height: 5px;
		border: none;
	}
	&-bar{
		top: 22px;
		height: 5px;
		border-radius: 2px;
		background: $color-red;
		border: none;
	}
	&-slider{
		top: 15px;
		width: 17px;
		height: 17px;
		border: none;
		background: #fff;
		border: 4px solid $color-red;
	}
}