.simple-button{
	display: flex;
	align-items: center;
	justify-content: center;
	max-width: 100%;
	border-radius: 30px;
	background-color: $color-red;
	border: 1px solid $color-red;

	color: #fff;
	font-size: 15px;
	font-weight: 600;
	transition: .3s;
	cursor: pointer;
	&:hover{
		background-color: transparent;
		color: $color-red;
	}
}