.product{
	&__top-info {
		padding: 0 0 40px;
	}
}

.product-title {
    color: #2d303f;
    font-size: 36px;
    font-weight: 700;
    text-transform: uppercase;
    line-height: 44px;
    margin-bottom: 30px;
}

.form-control {
	border-radius: 5px !important;
	&:focus {
		border-color: #ff7676 !important;
		box-shadow: 0 0 0 0.2rem rgba(255, 38, 28, .25) !important;
	}
}

.table-variable {
	box-shadow: 0 2px 7px rgba(0, 0, 0, 0.15);
    background-color: #ffffff;
	&__header {
		min-height: 26px;
		background-color: #e0e0e0;
		display: flex;
		padding: 5px 50px;
		align-items: center;
		justify-content: space-between;
	}
	&__row {
		display: flex;
		padding: 15px 50px;
		align-items: center;
		justify-content: space-between;
		position: relative;
		&:not(:last-child) {
			padding-bottom: 16px;
			&:after {
				content: "";
				position: absolute;
				bottom: 0;
				left: 20px;
				right: 20px;
				height: 1px;
				background-color: #d8d8d8;
			}
		}
	}
	&__column {
		&--first {
			width: 160px;
		}
		&--stars {
			width: 150px;
		}
		&--term {
			width: 100px;
		}
		&--price {
			width: 100px;
		}
		&--count {
			width: 100px;
		}
		&--cart {
			width: 160px;
		}
	}

	&__caption {
		color: #2d303f;
		font-size: 12px;
		font-weight: 400;
		line-height: 14px;
	}
	&__text {
		color: #2d303f;
		font-size: 15px;
		font-weight: 400;
		line-height: 20px;
	}
	&__price {
		color: #2d303f;
		font-size: 16px;
		font-weight: 700;
		line-height: 20px;
	}

	.product-amount {
		width: 100px;
		max-width: 100%;
		height: 34px;
	}
}

.stars {
    display: flex;
}

.star {
	width: 17px;
	height: 16px;

    display: flex;
    align-items: center;
    justify-content: center;
	&:not(:last-child) {
		margin-right: 5px;
	}
	&__img {
		width: 100%;
		height: 100%;
		object-fit:contain;
	}
}

.extra-products-slider .nav-buttons {
    padding-top: 15px;
}

@media(max-width: 1199px) {
	.table-variable {
		&__header {
			padding: 5px 20px;
		}
		&__row {
			padding: 10px 20px;
			&:not(:last-child) {
				padding-bottom: 11px;
			}
		}
		&__column {
			&--stars {
				width: 90px;
			}
			&--term {
				width: 70px;
			}
			&--price {
				width: 70px;
			}
			&--count {
				width: 80px;
			}
			&--cart {
				width: 120px;
			}
		}
	}
}

@media(max-width: 767px) {
	.product-title {
		font-size: 26px;
		line-height: 34px;
	}
	.product-form {
	}
	.table-variable {
		background-color: transparent;
		box-shadow: none;
		&__header {
			padding: 0 10px;
			.table-variable__column--count, .table-variable__column--cart {
				display: none;
			}
		}
		&__row {
			padding: 10px;
			flex-wrap: wrap;
			justify-content: space-evenly;
			background-color: #fff;
			box-shadow: 0 2px 7px rgba(0, 0, 0, 0.15);
			&:not(:last-child) {
				margin-bottom: 5px;
				&:after {
					bottom: 10px;
				}
			}
			&:after {
				content: "";
				position: absolute;
				bottom: 60px;
				margin: auto;
				left: 20px;
				right: 20px;
				height: 1px;
				background-color: #d8d8d8;
			}
		}
		&__column {
			&--first {
				width: 25%;
			}
			&--stars {
				width: 25%;
			}
			&--term {
				width: 25%;
			}
			&--price {
				width: 25%;
			}
			&--count {
				width: 100px;
				padding-top: 30px;
			}
			&--cart {
				width: 130px;
				padding-top: 30px;
			}
		}
	}
}

@media (max-width: 575px) {
	.product-title {
		font-size: 20px;
		line-height: 26px;
	}
	.product{
		&__top-info {
			padding: 0 0 15px;
		}
	}
	.star {
		width: 9px;
		height: 8px;
		&:not(:last-child) {
			margin-right: 2px;
		}
	}
	.table-variable {
		&__caption {
			font-size: 10px;
			line-height: 12px;
		}
		&__text {
			font-size: 13px;
			line-height: 16px;
		}
		&__price {
			font-size: 14px;
			line-height: 18px;
		}
	}
}